  top: -7px;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-image: url(../images/ice-coffee_mob.png);

  @media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
    background-image: url(../images/ice-coffee_mob-2x.png);
