.header-modal__input {
  display: flex;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  height: 32px;
  margin-bottom: 12px;
  padding: 8px 12px;

  background-color: rgba(248, 210, 78, 0.1);
  border: solid 1px $primary-bg-color;
  border-radius: 10px;

  font-size: 12px;
  line-height: 1.4;
  text-align: left;
  color: $tittle-text-color;

  @include tablet {
    width: 368px;
    height: 42px;
  }
}

.header-modal__comment {
  resize: none;
  height: 65px;
  margin-bottom: 26px;

  @include tablet {
    height: 84px;
  }
}

.submit-btn {
  display: flex;
  width: 114px;
  height: 42px;

  margin-right: auto;
  margin-left: auto;
  padding: 12px 34px;

  font-weight: 600;
  font-size: 12px;
  line-height: 1.17;
  background-color: $accent-color;

  cursor: pointer;
  border-radius: 10px;
  border: none;
}