@import '~node_modules/modern-normalize/modern-normalize.css';

html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}
body {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 1.86;
  color: $primary-text-color;
  background-color: $primary-bg-color;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0px;
  margin: 0;
}

.link {
  text-decoration: none;
  cursor: pointer;
}

// img {
//   display: block;
